<h3 id="qu-zhi"><a class="header-anchor" href="#qu-zhi" aria-hidden="true">¶</a> 取值</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-form :config=\&quot;config2\&quot; @submit=\&quot;submit2\&quot; ref=\&quot;form\&quot;&gt;&lt;/vue-form&gt; \n&lt;p&gt;your commit are: {{ values2 }} &lt;/p&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  import VueForm from '@lefreet/vue-form'\n  export default {\n    components: { VueForm },\n    data () {\n      config2: {\n        'values': {\n          'name': 'this is default value'\n        },\n        'buttons': [{\n          'text': '触发@submit事件',\n          'icon': 'el-icon-info',\n          'event': '@submit'\n        }, {\n          'text': '调用clearFields方法',\n          'icon': 'el-icon-success',\n          'event': 'clearFields'\n        }, {\n          'text': '重置',\n          'event': 'resetFields',\n          'type': 'success'\n        }],\n        'fields': [{\n          'type': 'input',\n          'required': true,\n          'prop': 'name'\n        }]\n      },\n      values2: {}\n    },\n    methods: {\n      submit2 (values) {\n        this.values2 = values\n      },\n      clear2 () {\n        this.$refs['form'].clearFields()\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-form :config="config2" @submit="submit2" ref="form"></vue-form> 
<p>your commit are: {{ values2 }} </p>


</body></html></div>
                            <p>表单通过<code>prop</code>自动生成<code>values</code>键值对；<code>@submit</code>监听表单提交事件，并获取验证通过的值；可直接调用实例的方法；<code>button</code>继承了<code>el-button</code>的所有属性，能够方便自定义；扩展了<code>event: '@xxx'</code>可以让表单触发内置的xxx事件，<code>event: xxx</code>可以直接调用表单的内置方法</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-form</span> <span class="hljs-attr">:config</span>=<span class="hljs-string">"config2"</span> <span class="hljs-attr">@submit</span>=<span class="hljs-string">"submit2"</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"form"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-form</span>&gt;</span> 
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>your commit are: {{ values2 }} <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> VueForm <span class="hljs-keyword">from</span> <span class="hljs-string">'@lefreet/vue-form'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueForm },
    data () {
      <span class="hljs-attr">config2</span>: {
        <span class="hljs-string">'values'</span>: {
          <span class="hljs-string">'name'</span>: <span class="hljs-string">'this is default value'</span>
        },
        <span class="hljs-string">'buttons'</span>: [{
          <span class="hljs-string">'text'</span>: <span class="hljs-string">'触发@submit事件'</span>,
          <span class="hljs-string">'icon'</span>: <span class="hljs-string">'el-icon-info'</span>,
          <span class="hljs-string">'event'</span>: <span class="hljs-string">'@submit'</span>
        }, {
          <span class="hljs-string">'text'</span>: <span class="hljs-string">'调用clearFields方法'</span>,
          <span class="hljs-string">'icon'</span>: <span class="hljs-string">'el-icon-success'</span>,
          <span class="hljs-string">'event'</span>: <span class="hljs-string">'clearFields'</span>
        }, {
          <span class="hljs-string">'text'</span>: <span class="hljs-string">'重置'</span>,
          <span class="hljs-string">'event'</span>: <span class="hljs-string">'resetFields'</span>,
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'success'</span>
        }],
        <span class="hljs-string">'fields'</span>: [{
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'input'</span>,
          <span class="hljs-string">'required'</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-string">'prop'</span>: <span class="hljs-string">'name'</span>
        }]
      },
      <span class="hljs-attr">values2</span>: {}
    },
    <span class="hljs-attr">methods</span>: {
      submit2 (values) {
        <span class="hljs-keyword">this</span>.values2 = values
      },
      clear2 () {
        <span class="hljs-keyword">this</span>.$refs[<span class="hljs-string">'form'</span>].clearFields()
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="ling-huo-bu-ju"><a class="header-anchor" href="#ling-huo-bu-ju" aria-hidden="true">¶</a> 灵活布局</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-form :config=\&quot;config3\&quot;&gt;&lt;/vue-form&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  import VueForm from '@lefreet/vue-form'\n  export default {\n    components: { VueForm },\n    data () {\n      config2: {\n        'cols': 3,\n        'label-width': '80px',\n        'btns-position': 'right',\n        'fields': [{\n          'type': 'input',\n          'prop': 'input1',\n          'label': 'input1',\n          'col': 3\n        }, {\n          'type': 'input',\n          'prop': 'input2',\n          'label': 'input2',\n          'col': 2\n        }, {\n          'type': 'input',\n          'prop': 'input3',\n          'label': 'input3',\n          'col': 1\n        }]\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-form :config="config3"></vue-form>


</body></html></div>
                            <p>通过config的<code>cols</code>和field的<code>col</code>属性，控制表单域布局；通过'btns-position'控制操作栏位置。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-form</span> <span class="hljs-attr">:config</span>=<span class="hljs-string">"config3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-form</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> VueForm <span class="hljs-keyword">from</span> <span class="hljs-string">'@lefreet/vue-form'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueForm },
    data () {
      <span class="hljs-attr">config2</span>: {
        <span class="hljs-string">'cols'</span>: <span class="hljs-number">3</span>,
        <span class="hljs-string">'label-width'</span>: <span class="hljs-string">'80px'</span>,
        <span class="hljs-string">'btns-position'</span>: <span class="hljs-string">'right'</span>,
        <span class="hljs-string">'fields'</span>: [{
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'input'</span>,
          <span class="hljs-string">'prop'</span>: <span class="hljs-string">'input1'</span>,
          <span class="hljs-string">'label'</span>: <span class="hljs-string">'input1'</span>,
          <span class="hljs-string">'col'</span>: <span class="hljs-number">3</span>
        }, {
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'input'</span>,
          <span class="hljs-string">'prop'</span>: <span class="hljs-string">'input2'</span>,
          <span class="hljs-string">'label'</span>: <span class="hljs-string">'input2'</span>,
          <span class="hljs-string">'col'</span>: <span class="hljs-number">2</span>
        }, {
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'input'</span>,
          <span class="hljs-string">'prop'</span>: <span class="hljs-string">'input3'</span>,
          <span class="hljs-string">'label'</span>: <span class="hljs-string">'input3'</span>,
          <span class="hljs-string">'col'</span>: <span class="hljs-number">1</span>
        }]
