<h3 id="chu-shi-hua"><a class="header-anchor" href="#chu-shi-hua" aria-hidden="true">¶</a> 初始化</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-form :config=\&quot;config4\&quot;&gt;&lt;/vue-form&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  import VueForm from '@lefreet/vue-form'\n  document.cookie = 'vueformstring=\&quot;this is a string from cookie\&quot;; path=/'\n  export default {\n    components: { VueForm },\n    data () {\n      config4: {\n        'buttons': [],\n        'values': {\n          'name': '#cookie.vueformstring'\n        },\n        'fields': [{\n          'prop': 'name',\n          'type': 'input'\n        }]\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-form :config="config4"></vue-form>


</body></html></div>
                            <p>组件内部用<code>js-cookie</code>和<code>query-string</code>获取了些静态数据，可以在配置的<code>values</code>节点，通过<code>#cookie.xxx</code>或者<code>#url.xxx</code>来获取些常用参数初始化到对应组件内，后面会根据需求扩展</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-form</span> <span class="hljs-attr">:config</span>=<span class="hljs-string">"config4"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-form</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
