  <span class="hljs-built_in">document</span>.cookie = <span class="hljs-string">'vueformstring="this is a string from cookie"; path=/'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueForm },
    data () {
      <span class="hljs-attr">config4</span>: {
        <span class="hljs-string">'buttons'</span>: [],
        <span class="hljs-string">'values'</span>: {
          <span class="hljs-string">'name'</span>: <span class="hljs-string">'#cookie.vueformstring'</span>
        },
        <span class="hljs-string">'fields'</span>: [{
          <span class="hljs-string">'prop'</span>: <span class="hljs-string">'name'</span>,
          <span class="hljs-string">'type'</span>: <span class="hljs-string">'input'</span>
        }]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="dan-kong-jian-yin-yong"><a class="header-anchor" href="#dan-kong-jian-yin-yong" aria-hidden="true">¶</a> 单控件引用</h3>
<p>你可能想单独引用某个控件(这和直接用element不是一样的？)，比如<code v-pre="">tree-select</code>(后面看看能不能提个pr给他们)</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;vue-tree :options=\&quot;treeOptions\&quot; v-model=\&quot;treeValue\&quot;&gt;&lt;/vue-tree&gt;\n&lt;p&gt;tree value is: {{ treeValue }} &lt;/p&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  import { VueTree } from '@lefreet/vue-form'\n  export default {\n    components: { VueTree },\n    treeOptions: {\n      'node-key': 'id',\n      'show-checkbox': true,\n      'value-type': 'leaf',\n      'data': [{\n        'id': 1,\n        'label': '福建',\n        'children': [{\n          'id': 2,\n          'label': '福州'\n        }, {\n          'id': 3,\n          'label': '厦门'\n        }]\n      }]\n    },\n    treeValue: ''\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head></head><body><vue-tree :options="treeOptions" v-model="treeValue"></vue-tree>
<p>tree value is: {{ treeValue }} </p>


</body></html></div>
                            <p>每个控件都统一实现了<code>options</code>属性和<code>v-model</code>绑定</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">vue-tree</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"treeOptions"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"treeValue"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-tree</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>tree value is: {{ treeValue }} <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">import</span> { VueTree } <span class="hljs-keyword">from</span> <span class="hljs-string">'@lefreet/vue-form'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">components</span>: { VueTree },
    <span class="hljs-attr">treeOptions</span>: {
      <span class="hljs-string">'node-key'</span>: <span class="hljs-string">'id'</span>,
      <span class="hljs-string">'show-checkbox'</span>: <span class="hljs-literal">true</span>,
      <span class="hljs-string">'value-type'</span>: <span class="hljs-string">'leaf'</span>,
      <span class="hljs-string">'data'</span>: [{
        <span class="hljs-string">'id'</span>: <span class="hljs-number">1</span>,
        <span class="hljs-string">'label'</span>: <span class="hljs-string">'福建'</span>,
        <span class="hljs-string">'children'</span>: [{
          <span class="hljs-string">'id'</span>: <span class="hljs-number">2</span>,
          <span class="hljs-string">'label'</span>: <span class="hljs-string">'福州'</span>
        }, {
          <span class="hljs-string">'id'</span>: <span class="hljs-number">3</span>,
          <span class="hljs-string">'label'</span>: <span class="hljs-string">'厦门'</span>
        }]
      }]
    },
    <span class="hljs-attr">treeValue</span>: <span class="hljs-string">''</span>
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="pei-zhi-wen-dang"><a class="header-anchor" href="#pei-zhi-wen-dang" aria-hidden="true">¶</a> 配置文档</h3>
<ul>
<li>下文<strong>from</strong>代表配置节点的属性来自<code v-pre="">element-ui</code>对应组件，可直接配置注入</li>
<li><code v-pre="">return</code>表示单个表单控件值的返回格式示例</li>
<li>表格中写出来的为扩展或者需要特殊说明的属性，其他属性点击链接查看官方文档就好</li>
</ul>
<h4 id="config-from-form-attributes"><a class="header-anchor" href="#config-from-form-attributes" aria-hidden="true">¶</a> config from <a href="http://element.eleme.io/#/zh-CN/component/form#form-attributes">Form Attributes</a></h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>cols</td>
<td>表单布局每一行分割的列数</td>
<td>number</td>
<td>-</td>
<td>2</td>
</tr>
<tr>
<td>buttons</td>
<td>表单的按钮集合</td>
<td>array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>btns-position</td>
<td>表单按钮栏的位置</td>
<td>string</td>
<td>left/top/right/bottom</td>
<td>bottom</td>
</tr>
<tr>
<td>fields</td>
<td>表单域的配置数组</td>
<td>array&lt;{}&gt;</td>
<td>见<code v-pre="">field</code>说明</td>
<td>[]</td>
</tr>
</tbody>
</table>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>clearFields</td>
<td>清空所有控件的值(注意清空和重置<code v-pre="">reset</code>的操作是不同的)</td>
<td>-</td>
</tr>
</tbody>
</table>
