<h4 id="options-of-select-from-select-attributes-return-a-b-c"><a class="header-anchor" href="#options-of-select-from-select-attributes-return-a-b-c" aria-hidden="true">¶</a> options of <code v-pre="">select</code> from <a href="http://element.eleme.io/#/zh-CN/component/select#select-attributes">Select Attributes</a> return <code v-pre="">'a,b,c'</code></h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
