<h4 id="options-of-switch-from-switch-attributes-return-true-false"><a class="header-anchor" href="#options-of-switch-from-switch-attributes-return-true-false" aria-hidden="true">¶</a> options of <code v-pre="">switch</code> from <a href="http://element.eleme.io/#/zh-CN/component/switch#attributes">Switch Attributes</a> return <code v-pre="">true/false</code></h4>
<h4 id="options-of-tree-from-tree-attributes-return-a-b-c"><a class="header-anchor" href="#options-of-tree-from-tree-attributes-return-a-b-c" aria-hidden="true">¶</a> options of <code v-pre="">tree</code> from <a href="http://element.eleme.io/#/zh-CN/component/tree#attributes">Tree Attributes</a> return <code v-pre="">'a,b,c'</code></h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value-type</td>
<td>被取值的节点类型</td>
<td>string</td>
<td>all/node/leaf</td>
<td>leaf</td>
</tr>
<tr>
<td>max-height</td>
<td>下拉框的最大高度</td>
<td>string</td>
<td>css height value</td>
<td>'auto'</td>
</tr>
<tr>
<td>api</td>
<td>数据接口，优先级大于data，并且支持扁平化的数据；该功能是针对项目业务做的扩展，可能问题比较大</td>
<td>object</td>
<td>-</td>
<td>undefined</td>
</tr>
<tr>
<td>node-key</td>
<td>节点取值的属性，<code v-pre="">必填</code></td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>show-checkbox</td>
<td>展示复选框，<code v-pre="">必填</code></td>
<td>boolean</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<ul>
<li>api参数</li>
</ul>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>接口地址</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>params</td>
<td>接口参数</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>接口返回的数据结构类型，是扁平类型时，内部会自动迭代成树形结构</td>
<td>string</td>
<td>flat</td>
<td>undefined</td>
</tr>
<tr>
<td>transform</td>
<td>接口返回值的key-map映射，供内部构建数据结构使用</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>transform.tree_label</td>
<td>树节点名称的key</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>transform.tree_id</td>
<td>数据构建树的唯一编号key</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>transform.tree_pid</td>
<td>数据构建树的父级编号key</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<ul>
<li>api示例</li>
</ul>
<pre v-pre=""><code class="hljs language-json">/* 接口配置 */
'api': {
  'url': '/api/v2/basic/data',
  'params': {
    'key': 's_comapi_virtual_group_tree',
    'start_with_tree_pid': '<span class="hljs-number">58</span>B8E19C9AA145E0852EA360C360CB9E'
  },
  'transform': {
    'id': 'id',
    'tree_label': 'name',
    'tree_id': 'id',
    'tree_pid': 'pid'
  },
  'type': 'flat'
}
/* 返回示例 */
[{
  'id': <span class="hljs-number">1</span>,
  'pid': <span class="hljs-number">0</span>
  'name': '福建'
}, {
  'id': <span class="hljs-number">2</span>,
  'pid': <span class="hljs-number">1</span>,
  'name': '福州'
}, {
  'id': <span class="hljs-number">3</span>,
  'pid': <span class="hljs-number">1</span>,
  'name': '厦门'
}]
</code></pre>
<ul>
<li>外部得到的数据经常是扁平化的，同接口例子的数据类似，所以组件提供了递归树形的方法（没作为内置方法是因为无法明确的判断传入的data是flat还是tree结构), eg:</li>
</ul>
<pre v-pre=""><code class="hljs language-js">&lt;vue-tree ref=<span class="hljs-string">"tree"</span> :data=<span class="hljs-string">"[]"</span> :options=<span class="hljs-string">"{}"</span>&gt;<span class="xml"><span class="hljs-tag">&lt;/<span class="hljs-name">vue-tree</span>&gt;</span>

this.$refs.tree.flat2tree(data, transform)
</span></code></pre>
<h4 id="options-of-upload-from-upload-attribute-return-file-id-1-2-3-from-api"><a class="header-anchor" href="#options-of-upload-from-upload-attribute-return-file-id-1-2-3-from-api" aria-hidden="true">¶</a> options of <code v-pre="">upload</code> from <a href="http://element.eleme.io/#/zh-CN/component/upload#attribute">Upload Attribute</a> return <code v-pre="">file.id '1,2,3' from api</code></h4>
